<template>
    <div class="fly-fail-record">
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>外部飞单列表</span>
                </div>
            </div>
            <div class="query-list">
                <span>时间：</span>
                <DatePicker v-model="searchDate" type="daterange" format="yyyy-MM-dd" placeholder="选择搜索的时间" class="search-date"></DatePicker>
                <div style="flex-grow: 1"></div>
                <div class="search-button" @click="getFlyFailRecord">
                    <span>查询</span>
                </div>
            </div>
            <div class="list-data">
                <div class="data-header">
                    <p class="item-info item-1">UUID</p>
                    <p class="item-info item-2">游戏</p>
                    <p class="item-info item-4">期号</p>
                    <p class="item-info item-5">注单内容</p>
                    <p class="item-info item-6">注单金额</p>
                    <p class="item-info item-7">注单时间</p>
                    <p class="item-info item-8">状态</p>
                </div>
                <div class="data-con">
                    <div class="data-item" v-for="(item, index) in showDataList" :key="index">
                        <p class="item-info item-1">{{item.uuid}}</p>
                        <p class="item-info item-3">{{item.gameName}}</p>
                        <p class="item-info item-4">{{item.perids}}</p>
                        <p class="item-info item-5" :title="item.currentBet">{{item.currentBet}}</p>
                        <p class="item-info item-6">{{item.bet}}</p>
                        <p class="item-info item-7">{{item.time}}</p>
                        <p class="item-info item-8">{{item.status}}</p>
                    </div>
                    <div v-if="!showDataList.length" class="no-data">
                        <span>暂无数据</span>
                    </div>
                </div>
            </div>
            <Page v-show="page.total" class="footer-page" :total="page.total" :page-size="page.pageSize" :current="page.pageNum" show-sizer @on-page-size-change="handleChangeSize" @on-change="handleChangePage"/>
        </div>
        <loading :loading="isLoading"></loading>
    </div>
</template>

<script>
export default {
    data() {
        return {
            showDataList: [],
            page: {
                total: 0,
                pageSize: 10,
                pageNum: 1
            },
            isLoading: false,
            searchDate: [new Date(), new Date(new Date().setDate(new Date().getDate() + 1))]
        }
    },
    mounted() {
        let that = this;
        that.getFlyFailRecord();
    },
    methods: {
        getFlyFailRecord() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/GetFlySheetFail',
                    params: {
                        startTime: that.searchDate[0].pattern('yyyy-MM-dd') + ' 00:00',
                        endTime: that.searchDate[1].pattern('yyyy-MM-dd') + ' 23:59',
                        start: that.page.pageNum,
                        pageSize: that.page.pageSize
                    }
                }).then(function(data) {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        const result = data.data.Data;
                        that.page.total = data.data.Total;
                        that.showDataList = result.map(function(ele) {
                            let currentBets = ele.Orders.map((ite) => ite.content).join(' ');
                            let bet = ele.Orders.map((ite) => ite.money).join(' ');
                            return {
                                uuid: ele.UUID,
                                gameName: ele.GameName,
                                perids: ele.Nper,
                                currentBet: currentBets,
                                bet: bet,
                                time: ele.Time,
                                status: ele.Status
                            }
                        })
                    }
                })
            }
        },
        handleChangeSize(_pageSize) {
            let that = this;

            that.page.pageSize = _pageSize;
            that.page.pageNum = 1;
            that.getFlyFailRecord();
        },
        handleChangePage(page) {
            let that = this;

            that.page.pageNum = page;
            that.getFlyFailRecord();
        },
    }
}
</script>

<style lang="scss" scoped>
.fly-fail-record {
    position: relative;
    .content {
        flex-direction: column;

        .list-header {
            .list-title {
                justify-content: space-between;

                width: 100%;

                overflow: hidden;
                .button {
                    display: inline-block;
                    width: 80px;
                    height: 32px;
                    line-height: 32px;
                    text-align: center;
                    border-radius: 4px;
                    background-color: #0078ff;
                    cursor: pointer;
                    vertical-align: middle;
                    
                    & > span {
                        color: #ffffff;
                    }
                }
            }
        }

        .query-list {
            .connection-info {
                display: flex;
                justify-content: space-around;
                margin-top: 10px;
                padding: 8px 2px;
                
                border: solid 1px #e4e4e4;

                .connection-item {
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    & > span {
                        margin-right: 3px;
                        word-break: keep-all;
                        font-size: 12px;
                    }
                }

                .button {
                    width: 60px;
                    height: 32px;
                    border-radius: 4px;
                    cursor: pointer;
                    
                    & > span {
                        color: #ffffff;
                    }
                }

                .login {
                    background-color: blue;
                }

                .exit {
                    background-color: red;
                }
            }

            .sync-info {
                display: flex;
                justify-content: space-around;
                margin-top: 10px;
                
                border: solid 1px #e4e4e4;

                .sync-item {
                    flex-grow: 1;
                    border-right: solid 1px #e4e4e4;

                    & > p {
                        height: 40px;
                        line-height: 40px;
                        text-align: center;
                    }

                    & > p:first-child {
                        border-bottom: solid 1px #e4e4e4;
                    }
                }
            }
        }

        .list-data {
            margin-top: 15px;

            width: 100%;

            border: solid 1px #d7d7d7;

            .data-header, .data-item {
                display: flex;
                align-items: stretch;

